import React from "react";
import homeLogo from "../images/home-main.svg"
import { Text } from "@chakra-ui/react";
import Type from "./type";
import { Button } from "@chakra-ui/react";


const Home = () => {

  const onButtonClick = () => {
    window.open();
  }; 

  return (
    <div id="home">
    <div  style={{height:"500px",display:"flex",justifyContent:"space-around"}}>
      <div>
       <h1 style={{ paddingBottom: 15 }} className="heading">
               Hi There!{"  "}
                <span className="wave" role="img" aria-labelledby="wave">
                  👋🏻
                </span>
              </h1>
              <h1 className="heading-name">
                My Name is 
                <strong className="main-name">    ARPIT KUMAR</strong>
              </h1>
              <div className="main-name"style={{ padding: 50, textAlign: "center", fontSize :" 2.4em"  }}>
                <Type />
              </div>
              <Text className="heading-name">Download
              
              <strong className="main-name">  Resume:-</strong></Text>
              <Button
                backgroundColor="#cd5ff8"
                _hover={{ bg: "#a891b7", color: "black" }}
                color="white"
                variant="solid"
                onClick={onButtonClick}
                size={["sm", "md"]}
                download="Shubham_Verma_Resume"
                
                id="resume-button-2"
                mt="10px"
                color= "black"
              >
                <a
                  href="#"
                  target="_blank"
                  download="#"
                >
                  RESUME
                </a>
               
              </Button>
              </div>
      <div className="home-img">
       <img
                src={homeLogo}
                alt="home pic"
                className="img-fluid"
                style={{ maxHeight: "450px" }}
              />
              </div>
             
    </div>
   
    </div>
  );
};

export default Home;
