import React from "react";
import homeLogo from "../images/myimagelite.jpg"

const About = () => {
  return (
    <div id="about" style={{height:"600px",display:"flex",justifyContent:"space-around"}}>
    <div  className="about-section" >
      <h4 id="user-detail-intro" style={{ fontSize: "25px",marginLeft:"30px",marginRight:"30px" }} >
          Hello, I'm{" "}
          <span style={{ color: "#cd5ff8" }}>Arpit Kumar</span>, a determined and versatile Full Stack MERN developer.<br/> With a passion for web development and a solid foundation in{" "}
          <span style={{ color: "#cd5ff8" }}>MongoDB, Express.js, React.js,</span> and <span style={{ color: "#cd5ff8" }}>Nodejs</span> I'm committed to creating impactful and user-centric applications.<br/> I thrive on continuous learning, embracing challenges, and collaborating with like-minded professionals to build innovative solutions.<br/> Let's connect and explore the possibilities together.
        </h4>
    </div>
    <div className="home-img">
       <img
                src={homeLogo}
                alt="home pic"
                className="img-fluid"
                style={{ maxHeight: "450px" }}
              />
              </div>
    </div>
  );
};

export default About;
